import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getDashboardData } from "../../reducers/dashboard";
import Spinner from "../../components/Spinner";
import DatePickers from "../DatePicker";
import { APP_URLS } from "../../helpers/routes";
import boy11 from "../../assets/theme/media/svg/avatars/029-boy-11.svg";
import { Link } from "react-router-dom";
import SortIcon from "@material-ui/icons/ArrowDownward";
import DataTable from "react-data-table-component";
import {
  getTodayDate,
  formatDateTimeFromUtcDate,
  formatCurrency,
} from "../../utils/helpers";
import { saleTypeMapper } from "../../helpers/constants";

const Dashboard = () => {
  const dispatch = useDispatch();

  const { token } = useSelector((state) => state.auth);

  const { data, dashboardRequestStatus } = useSelector(
    (state) => state.dashboard
  );

  const [fromDate, setFromDate] = useState(getTodayDate());
  const handleFromDateChange = (date) => {
    setFromDate(date);
  };

  const [toDate, setToDate] = useState(getTodayDate());
  const handleToDateChange = (date) => {
    setToDate(date);
  };

  const handleUpdateDashboard = () => {
    dispatch(
      getDashboardData({ token, start_date: fromDate, end_date: toDate })
    );
  };

  useEffect(() => {
    dispatch(
      getDashboardData({ token, start_date: fromDate, end_date: toDate })
    );
  }, []);

  if (!data || dashboardRequestStatus !== "succeeded") {
    return <Spinner />;
  }

  const columns = [
    {
      name: "ID",
      selector: (row) => row.id,
      sortable: true,
    },
    {
      name: "Tipo de venta",
      selector: (row) => row.sale_type,
      sortable: true,
    },
    {
      name: "Encargado",
      selector: (row) => row.username,
      sortable: true,
    },
    {
      name: "Cliente",
      selector: (row) => row.customer,
      sortable: true,
    },
    {
      name: "Total",
      selector: (row) => row.total,
      sortable: true,
    },
    {
      name: "Fecha y hora de venta",
      selector: (row) => row.sale_date,
      sortable: true,
    },
    {
      name: "Acciones",
      selector: (row) => row.action,
    },
  ];

  const tableData = data.sales.map((sale) => ({
    id: sale.id,
    sale_type: saleTypeMapper[sale.sale_type],
    username: sale.user.username,
    customer: sale.customer
      ? `
      ${sale.customer.nit} - 
      ${sale.customer.first_name} - 
      ${sale.customer.last_name}
    `
      : "C/F",
    total: formatCurrency(sale.total),
    sale_date: formatDateTimeFromUtcDate(sale.sale_date),
    action: (
      <Link to={`/${APP_URLS.reports.sales}/${sale.id}`}>
        <button title="Ver detalle" className="btn btn-icon btn-warning">
          <i className="flaticon-edit-1"></i>
        </button>
      </Link>
    ),
  }));

  const paginationComponentOptions = {
    selectAllRowsItem: true,
    selectAllRowsItemText: "ALL",
  };

  return (
    <div>
      <div className="row py-4">
        <div className="col-xl-4">
          <DatePickers
            id="from-date"
            label="Fecha inicio"
            value={fromDate}
            onChange={handleFromDateChange}
          />
        </div>
        <div className="col-xl-4">
          <DatePickers
            id="to-date"
            label="Fecha fin"
            value={toDate}
            onChange={handleToDateChange}
          />
        </div>
        <div className="col-xl-4">
          <button
            type="button"
            className="btn btn-primary w-100"
            onClick={handleUpdateDashboard}
          >
            Actualizar Dashboard
          </button>
        </div>
      </div>
      <div className="row">
        <div className="col-xl-4">
          <div className="card card-custom card-stretch gutter-b">
            <div className="card-body d-flex align-items-center py-0 mt-8">
              <div className="d-flex flex-column flex-grow-1 py-2 py-lg-5">
                <div className="card-title font-weight-bolder text-dark-75 font-size-h5 mb-2 text-hover-primary">
                  Total de clientes
                </div>
                <span className="font-weight-bold text-muted font-size-lg">
                  {data.customers_count}
                </span>
              </div>
              <img src={boy11} alt="" className="align-self-end h-100px" />
            </div>
          </div>
        </div>
        <div className="col-xl-4">
          <div className="card card-custom bg-danger bg-hover-state-danger card-stretch gutter-b">
            <div className="card-body">
              <span className="svg-icon svg-icon-white svg-icon-3x ml-n1">
                <svg
                  width="24px"
                  height="24px"
                  viewBox="0 0 24 24"
                  version="1.1"
                >
                  <g
                    stroke="none"
                    strokeWidth="1"
                    fill="none"
                    fillRule="evenodd"
                  >
                    <rect x="0" y="0" width="24" height="24"></rect>
                    <path
                      d="M12,4.56204994 L7.76822128,9.6401844 C7.4146572,10.0644613 6.7840925,10.1217854 6.3598156,9.76822128 C5.9355387,9.4146572 5.87821464,8.7840925 6.23177872,8.3598156 L11.2317787,2.3598156 C11.6315738,1.88006147 12.3684262,1.88006147 12.7682213,2.3598156 L17.7682213,8.3598156 C18.1217854,8.7840925 18.0644613,9.4146572 17.6401844,9.76822128 C17.2159075,10.1217854 16.5853428,10.0644613 16.2317787,9.6401844 L12,4.56204994 Z"
                      fill="#000000"
                      fillRule="nonzero"
                      opacity="0.3"
                    ></path>
                    <path
                      d="M3.5,9 L20.5,9 C21.0522847,9 21.5,9.44771525 21.5,10 C21.5,10.132026 21.4738562,10.2627452 21.4230769,10.3846154 L17.7692308,19.1538462 C17.3034221,20.271787 16.2111026,21 15,21 L9,21 C7.78889745,21 6.6965779,20.271787 6.23076923,19.1538462 L2.57692308,10.3846154 C2.36450587,9.87481408 2.60558331,9.28934029 3.11538462,9.07692308 C3.23725479,9.02614384 3.36797398,9 3.5,9 Z M12,17 C13.1045695,17 14,16.1045695 14,15 C14,13.8954305 13.1045695,13 12,13 C10.8954305,13 10,13.8954305 10,15 C10,16.1045695 10.8954305,17 12,17 Z"
                      fill="#000000"
                    ></path>
                  </g>
                </svg>
              </span>
              <div className="text-inverse-danger font-weight-bolder font-size-h5 mb-2 mt-5">
                Ventas realizadas
              </div>
              <div className="font-weight-bold text-inverse-danger font-size-sm">
                {data.sales_count}
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-4">
          <div className="card card-custom bg-success bg-hover-state-success card-stretch gutter-b">
            <div className="card-body">
              <span className="svg-icon svg-icon-white svg-icon-3x ml-n1">
                <svg
                  width="24px"
                  height="24px"
                  viewBox="0 0 24 24"
                  version="1.1"
                >
                  <g
                    stroke="none"
                    strokeWidth="1"
                    fill="none"
                    fillRule="evenodd"
                  >
                    <rect x="0" y="0" width="24" height="24"></rect>
                    <rect
                      fill="#000000"
                      opacity="0.3"
                      x="13"
                      y="4"
                      width="3"
                      height="16"
                      rx="1.5"
                    ></rect>
                    <rect
                      fill="#000000"
                      x="8"
                      y="9"
                      width="3"
                      height="11"
                      rx="1.5"
                    ></rect>
                    <rect
                      fill="#000000"
                      x="18"
                      y="11"
                      width="3"
                      height="9"
                      rx="1.5"
                    ></rect>
                    <rect
                      fill="#000000"
                      x="3"
                      y="13"
                      width="3"
                      height="7"
                      rx="1.5"
                    ></rect>
                  </g>
                </svg>
              </span>
              <div className="text-inverse-success font-weight-bolder font-size-h5 mb-2 mt-5">
                Compras realizadas
              </div>
              <div className="font-weight-bold text-inverse-success font-size-sm">
                {data.purchases_count}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-xl-4">
          <div className="card card-custom bg-primary bg-hover-state-primary card-stretch gutter-b">
            <div className="card-body">
              <span className="svg-icon svg-icon-white svg-icon-3x ml-n1">
                <svg
                  width="24px"
                  height="24px"
                  viewBox="0 0 24 24"
                  version="1.1"
                >
                  <g
                    stroke="none"
                    strokeWidth="1"
                    fill="none"
                    fillRule="evenodd"
                  >
                    <rect x="0" y="0" width="24" height="24"></rect>
                    <rect
                      fill="#000000"
                      x="4"
                      y="4"
                      width="7"
                      height="7"
                      rx="1.5"
                    ></rect>
                    <path
                      d="M5.5,13 L9.5,13 C10.3284271,13 11,13.6715729 11,14.5 L11,18.5 C11,19.3284271 10.3284271,20 9.5,20 L5.5,20 C4.67157288,20 4,19.3284271 4,18.5 L4,14.5 C4,13.6715729 4.67157288,13 5.5,13 Z M14.5,4 L18.5,4 C19.3284271,4 20,4.67157288 20,5.5 L20,9.5 C20,10.3284271 19.3284271,11 18.5,11 L14.5,11 C13.6715729,11 13,10.3284271 13,9.5 L13,5.5 C13,4.67157288 13.6715729,4 14.5,4 Z M14.5,13 L18.5,13 C19.3284271,13 20,13.6715729 20,14.5 L20,18.5 C20,19.3284271 19.3284271,20 18.5,20 L14.5,20 C13.6715729,20 13,19.3284271 13,18.5 L13,14.5 C13,13.6715729 13.6715729,13 14.5,13 Z"
                      fill="#000000"
                      opacity="0.3"
                    ></path>
                  </g>
                </svg>
              </span>
              <div className="text-inverse-primary font-weight-bolder font-size-h5 mb-2 mt-5">
                Unidades vendidas
              </div>
              <div className="font-weight-bold text-inverse-primary font-size-sm">
                {data.sale_items_sum}
              </div>
            </div>
          </div>
        </div>
        <div className="col-xl-4">
          <div className="card card-custom bgi-no-repeat card-stretch gutter-b">
            <div className="card-body">
              <span className="svg-icon svg-icon-2x svg-icon-info">
                <svg
                  width="24px"
                  height="24px"
                  viewBox="0 0 24 24"
                  version="1.1"
                >
                  <g
                    stroke="none"
                    strokeWidth="1"
                    fill="none"
                    fillRule="evenodd"
                  >
                    <rect x="0" y="0" width="24" height="24"></rect>
                    <path
                      d="M12,4.56204994 L7.76822128,9.6401844 C7.4146572,10.0644613 6.7840925,10.1217854 6.3598156,9.76822128 C5.9355387,9.4146572 5.87821464,8.7840925 6.23177872,8.3598156 L11.2317787,2.3598156 C11.6315738,1.88006147 12.3684262,1.88006147 12.7682213,2.3598156 L17.7682213,8.3598156 C18.1217854,8.7840925 18.0644613,9.4146572 17.6401844,9.76822128 C17.2159075,10.1217854 16.5853428,10.0644613 16.2317787,9.6401844 L12,4.56204994 Z"
                      fill="#000000"
                      fillRule="nonzero"
                      opacity="0.3"
                    ></path>
                    <path
                      d="M3.5,9 L20.5,9 C21.0522847,9 21.5,9.44771525 21.5,10 C21.5,10.132026 21.4738562,10.2627452 21.4230769,10.3846154 L17.7692308,19.1538462 C17.3034221,20.271787 16.2111026,21 15,21 L9,21 C7.78889745,21 6.6965779,20.271787 6.23076923,19.1538462 L2.57692308,10.3846154 C2.36450587,9.87481408 2.60558331,9.28934029 3.11538462,9.07692308 C3.23725479,9.02614384 3.36797398,9 3.5,9 Z M12,17 C13.1045695,17 14,16.1045695 14,15 C14,13.8954305 13.1045695,13 12,13 C10.8954305,13 10,13.8954305 10,15 C10,16.1045695 10.8954305,17 12,17 Z"
                      fill="#000000"
                    ></path>
                  </g>
                </svg>
              </span>
              <span className="card-title font-weight-bolder text-dark-75 font-size-h2 mb-0 mt-6 d-block">
                {formatCurrency(data.sales_total, true)}
              </span>
              <span className="font-weight-bold text-muted font-size-sm">
                Total en Ventas
              </span>
            </div>
          </div>
        </div>
        <div className="col-xl-4">
          <div className="card card-custom bgi-no-repeat card-stretch gutter-b">
            <div className="card-body">
              <span className="svg-icon svg-icon-2x svg-icon-info">
                <svg
                  width="24px"
                  height="24px"
                  viewBox="0 0 24 24"
                  version="1.1"
                >
                  <g
                    stroke="none"
                    strokeWidth="1"
                    fill="none"
                    fillRule="evenodd"
                  >
                    <rect x="0" y="0" width="24" height="24"></rect>
                    <rect
                      fill="#000000"
                      opacity="0.3"
                      x="13"
                      y="4"
                      width="3"
                      height="16"
                      rx="1.5"
                    ></rect>
                    <rect
                      fill="#000000"
                      x="8"
                      y="9"
                      width="3"
                      height="11"
                      rx="1.5"
                    ></rect>
                    <rect
                      fill="#000000"
                      x="18"
                      y="11"
                      width="3"
                      height="9"
                      rx="1.5"
                    ></rect>
                    <rect
                      fill="#000000"
                      x="3"
                      y="13"
                      width="3"
                      height="7"
                      rx="1.5"
                    ></rect>
                  </g>
                </svg>
              </span>
              <span className="card-title font-weight-bolder text-dark-75 font-size-h2 mb-0 mt-6 d-block">
                {formatCurrency(data.purchases_total, true)}
              </span>
              <span className="font-weight-bold text-muted font-size-sm">
                Total en Compras
              </span>
            </div>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-xl-12">
          <DataTable
            title="Listado de ventas"
            columns={columns}
            data={tableData}
            sortIcon={<SortIcon />}
            pagination
            paginationComponentOptions={paginationComponentOptions}
            allowRowEvents
          />
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
